.stac_card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.staclc_card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  padding: 0.8rem;
  margin-top: 0.9rem;
}
.staclc_card_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.staclc_card > img,
.staclc_card_container > img {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
}
.staclc_card_inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.staclc_card_inner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.staclc_card_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
.stc_stats {
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
  color: #001a72;
}
.stc_stats > span {
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  border-radius: 6px;
  transition: all 300ms ease-in 50ms;
}
.stc_stats > span:hover {
  background-color: #001a72;
  color: #ffffff;
}
.stcs_stats {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #001a72;
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  border-radius: 6px;
  transition: all 300ms ease-in 50ms;
}
.stcs_stats:hover {
  background-color: #001a72;
  color: #ffffff;
}
.stcs_stats > div,
.stcs_stats_without > div {
  margin-bottom: 0.2rem;
}
.stcs_stats_without {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #001a72;
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  border-radius: 6px;
  transition: all 300ms ease-in 50ms;
}
